import "./Display.scss";
import "../Chat/Chat.scss";
import { Message } from "../Message";
import { Received_message } from "../";
import React, { useEffect, useRef } from 'react';
import { Centrifuge } from 'centrifuge';
import {connect} from 'react-redux';
import { getMessage } from '../../actions';

function beep_first() {
    var snd = new Audio("data:audio/wav;base64,");  
    snd.volume = 0.005;
    snd.autoplay = true;
}

let centrifuge = new Centrifuge('ws://127.0.0.1:8000/connection/websocket');
let sse;
let messages = [];
export function Display(props) {
    beep_first();
    let address = (props.id == "tt-front") ? "https://tt-front.vercel.app/messages" : `http://127.0.0.1:7000/chats/message_list_add/?id_chat=${props.id}`;
    useEffect(() => {
        if(props.id != "tt-front"){
            props.getMessage(address, props.name, props.id, centrifuge);
        }else{
            sse =  new EventSource('http://localhost:9000/messages-sse');
            props.getMessage(address, props.name, props.id, sse);
        }
        return function cleanup() {
            if(props.id == "tt-front"){
                sse.close();
            }else{
                for (let sub in centrifuge.subscriptions()){
                    centrifuge.removeSubscription(centrifuge.subscriptions()[sub]);
                }
            }
            messages = [];
        }
    }, []); 
    if(props.message['chat'] == props.name && !props.loading){
        let len = messages.length
        if(props.id == "tt-front"){
            for(var i = 0; i < props.message['content'].length; i++){
                let date = new Date(props.message['content'].at(i).timestamp)
                let key = i+len.toString();
                if(props.message['content'].at(i).author == 'Avotalopsi'){
                    messages = [...messages, <Message key = {key}
                                                       message = {props.message['content'].at(i).text}
                                                       time_loc = {`${date.toString().substring(16, 21)}`}
                                />];
                }
                else{
                    messages = [...messages, <Received_message key = {key}
                                                                message = {props.message['content'].at(i).text}
                                                                time_author = {`${props.message['content'].at(i).author} ${date.toString().substring(16, 21)}`}
                                />];
                }
            }
        }else{
            for(var i = props.message['content'].length-1; i >= 0; i--){
                let date = new Date(props.message['content'].at(i).date_create);
                let key = i+len.toString();
                if(props.message['content'].at(i).sender == 'Avotalopsi'){
                    messages = [...messages, <Message key = {key}
                                                       message = {props.message['content'].at(i).content}
                                                       time_loc = {`${date.toString().substring(16, 21)}`}
                                />];
                }
                else{
                    messages = [...messages, <Received_message key = {key}
                                                                message = {props.message['content'].at(i).content}
                                                                time_author = {`${props.message['content'].at(i).sender} ${date.toString().substring(16, 21)}`}
                                />];
                }
            }
        }    
    }
    const messagesEndRef = useRef(null);
    const scrollToBottom = () => {
        messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
    }
    useEffect(() => {
        scrollToBottom();
    }, [messages]);
    return(
        <div className="display">
            {messages}
            <div ref={messagesEndRef} />
        </div>
    );
}

const mapStateProps = (state) => ({
    message: state.message.message,
    loading: state.message.loading
})

let DisplayConnect = connect(mapStateProps, {getMessage})(Display);
export default DisplayConnect;
